import './stubs';
import CustomNotices from './Notices';

const { useDispatch } = wp.data;
const { useShortcut, store } = wp.keyboardShortcuts;
const { link, handleAutosaves } = window.revisions;
const { createSlotFill, Button } = wp.components;
const { applyFilters } = wp.hooks;
const { Fill } = createSlotFill('PinnedPlugins');
const { __ } = wp.i18n;

/**
 * Render revisions icon in the editor header
 *
 * Simulates what the block editor does with the "ComplementaryAreaToggle" component
 * @see https://github.com/WordPress/gutenberg/blob/9f17d1daf19472d0808024a8f918df118d18f26d/packages/interface/src/components/complementary-area/index.js#L155
 * @see https://github.com/WordPress/gutenberg/blob/9f17d1daf19472d0808024a8f918df118d18f26d/packages/interface/src/components/complementary-area-toggle/index.js#L32
 * @returns {wp.element.Component} DisplayComponent
 */
const DisplayComponent = () => {
  const { registerShortcut } = useDispatch(store);
  const enableNewTab = applyFilters('newspress.revisions.openInNewWindow', false);
  const target = enableNewTab ? '_blank' : '_self';
  const label = enableNewTab
    ? __('Open revisions in new window', 'newspress')
    : __('Open revisions', 'newspress');

  registerShortcut({
    name: 'nuk.newspress.revisions.sidebar',
    description: 'Go to revisions',
    category: 'global',
    keyCombination: {
      modifier: 'primaryAlt',
      character: 'r',
    },
  });

  useShortcut('nuk.newspress.revisions.sidebar', () => {
    window.open(link, target);
  });

  return (
    <>
      <Fill name="PinnedItems/core/edit-post">
        <Button
          className="revisions-toolbar-icon"
          href={link}
          target={target}
          icon="backup"
          identifier="newspress-revisions/newspress-revisions"
          label={label}
          shortcut={{
            character: 'r',
            modifier: 'primaryAlt',
          }}
          showTooltip
        />
      </Fill>
      {handleAutosaves && <CustomNotices />}
    </>
  );
};

export default DisplayComponent;
