const { apiFetch } = wp;

/**
 * prepend revisions api namespace to route.
 * @param {string} route - route to be appended.
 * @returns {string} full namespaced route.
 */
const path = (route) => `revisions/v1/${route}`;

/**
 * Update autosave with additional data.
 * @param {number} autosaveId - autosave id.
 * @param {object} data - data to send with request.
 * @returns {object} api response
 */
const updateAutosave = (autosaveId, data = {}) =>
  apiFetch({
    path: path(`autosave/${autosaveId}`),
    method: 'POST',
    data,
  });

export default {
  updateAutosave,
};
