import PropTypes from 'prop-types';
import { postKey } from './stubs';

const { link } = window.revisions;
const { __ } = wp.i18n;
const { compose } = wp.compose;
const { withSelect, withDispatch } = wp.data;
const { parse } = wp.blocks;

/**
 * Component manages replacing of WordPress autosaves notices with custom
 * ones, so that we have control over the message and actions of the notice
 * (allows us to link to custom revisions view).
 * @param {object} props props object.
 * @param {number} props.postId current post id.
 * @param {boolean} props.isEditedPostNew true if the currently edited post is yet to be saved, or false if the post has been saved.
 * @param {Array} props.notices array of notices.
 * @param {Function} props.createNotice Returns an action object used in signalling that a notice is to be created.
 * @param {Function} props.removeNotice Returns an action object used in signalling that a notice is to be removed.
 * @param {Function} props.editPost Returns an action object used in signalling that attributes of the post have been edited.
 * @param {Function} props.resetEditorBlocks Returns an action object used to signal that the blocks have been updated.
 * @returns {null} no ouput.
 */
export const Notices = ({
  postId,
  isEditedPostNew,
  notices,
  createNotice,
  removeNotice,
  editPost,
  resetEditorBlocks,
}) => {
  const autosaveNotice = notices?.filter?.((notice) => notice.id === 'autosave-exists');
  const localAutosaveNotice = notices?.filter?.((notice) =>
    notice?.id?.includes('wpEditorAutosaveRestore'),
  );

  /**
   * Handle autosaves, saved in database.
   *
   * Autosaves will get saved to database if the user has a valid session.
   */
  if (autosaveNotice.length > 0) {
    // Remove core WordPress autosave notice.
    removeNotice(autosaveNotice[0].id);

    const message = __(
      'There is an autosave of this post that is more recent than the version below.',
      'newspress',
    );

    // Create custom autosave notice, linking action to custom revisions view.
    createNotice('warning', message, {
      id: 'newspress-autosave-exists',
      actions: [
        {
          label: __('View the autosave', 'newspress'),
          url: link,
        },
      ],
    });
  }

  /**
   * Handle autosaves, saved in local storage.
   *
   * Autosaves will get saved to local storage if the user does NOT have a valid session.
   */
  if (localAutosaveNotice.length > 0) {
    // Remove core WordPress autosave notice.
    removeNotice(localAutosaveNotice[0].id);

    const message = __(
      'The backup of this post in your browser is different from the version below.',
      'newspress',
    );

    /**
     * Create custom local storage notice that lets us restore the additional data we saved.
     *
     * Ref: https://github.com/WordPress/gutenberg/blob/e1c720c4ffefa0812be6b2963527722eb80f0469/packages/editor/src/components/local-autosave-monitor/index.js#L106
     */
    createNotice('warning', message, {
      id: 'newspress-local-autosave-exists',
      actions: [
        {
          label: __('Restore from backup', 'newspress'),
          /**
           * onClick restore current post using data saved to local storage.
           */
          onClick() {
            const localAutosave = JSON.parse(
              window.sessionStorage.getItem(postKey(postId, isEditedPostNew)),
            );

            const { post_title: title, content, excerpt, meta } = localAutosave;
            const edits = { title, content, excerpt, meta };

            const { content: editsContent, ...editsWithoutContent } = edits;
            editPost(editsWithoutContent);
            resetEditorBlocks(parse(edits.content));
            removeNotice('newspress-local-autosave-exists');
          },
        },
      ],
    });
  }

  return null;
};

const composeWithSelect = withSelect((select) => {
  const store = select('core/notices');

  return {
    notices: store.getNotices(),
    postId: select('core/editor').getCurrentPost()?.id,
    isEditedPostNew: select('core/editor').isEditedPostNew(),
  };
});

const composeWithDispatch = withDispatch((dispatch) => {
  const store = dispatch('core/notices');
  const { createNotice, removeNotice } = store;

  const editorStore = dispatch('core/editor');
  const { editPost, resetEditorBlocks } = editorStore;

  return {
    // core ref: https://developer.wordpress.org/block-editor/reference-guides/data/data-core-notices/#createnotice
    createNotice,
    // core ref: https://developer.wordpress.org/block-editor/reference-guides/data/data-core-notices/#removenotice
    removeNotice,
    editPost,
    resetEditorBlocks,
  };
});

Notices.propTypes = {
  postId: PropTypes.number,
  isEditedPostNew: PropTypes.bool,
  notices: PropTypes.array,
  createNotice: PropTypes.func,
  removeNotice: PropTypes.func,
  editPost: PropTypes.func,
  resetEditorBlocks: PropTypes.func,
};

export default compose(composeWithSelect, composeWithDispatch)(Notices);
