/* eslint-disable import/prefer-default-export */
import api from './utils/api';

const { handleAutosaves } = window.revisions;
const { select, dispatch } = wp.data;

/**
 * Function returning a sessionStorage key to set or retrieve a given post's
 * automatic session backup.
 *
 * Keys are crucially prefixed with 'wp-autosave-' so that wp-login.php's
 * `loggedout` handler can clear sessionStorage of any user-private content.
 * @see https://github.com/WordPress/wordpress-develop/blob/6dad32d2aed47e6c0cf2aee8410645f6d7aba6bd/src/wp-login.php#L103
 * @param {string}  postId    Post ID.
 * @param {boolean} isPostNew Whether post new.
 * @returns {string} sessionStorage key
 */
export const postKey = (postId, isPostNew) =>
  `wp-autosave-block-editor-post-${isPostNew ? 'auto-draft' : postId}`;

/**
 * Saves passed data to local storage against custom key.
 * @param {*} postId post id.
 * @param {boolean} isPostNew isPostNew.
 * @param {string} title title to save to local storage.
 * @param {string} content content to save to local storage.
 * @param {string} excerpt exceprt to save to local storage.
 * @param {object} meta post meta to save to local storage.
 */
const localAutosaveSet = (postId, isPostNew, title, content, excerpt, meta) => {
  window.sessionStorage.setItem(
    postKey(postId, isPostNew),
    JSON.stringify({
      post_title: title,
      content,
      excerpt,
      meta,
    }),
  );
};

// Override autosave function if autosave integration is enabled.
if (handleAutosaves) {
  /**
   * Replaces core store method so that we can save additional data (meta) to
   * local storage.
   *
   * Ref: https://github.com/WordPress/gutenberg/blob/e1c720c4ffefa0812be6b2963527722eb80f0469/packages/editor/src/store/actions.js#L277
   *
   * Action that autosaves the current post.  This
   * includes server-side autosaving (default) and client-side (a.k.a. local)
   * autosaving (e.g. on the Web, the post might be committed to Session
   * Storage).
   * @param {object?} options Extra flags to identify the autosave.
   */
  wp.data.dispatch('core/editor').autosave = async ({ local = false, ...options } = {}) => {
    if (local) {
      const post = select('core/editor').getCurrentPost();
      const isPostNew = select('core/editor').isEditedPostNew();
      const title = select('core/editor').getEditedPostAttribute('title');
      const content = select('core/editor').getEditedPostAttribute('content');
      const excerpt = select('core/editor').getEditedPostAttribute('excerpt');
      const meta = select('core/editor').getEditedPostAttribute('meta');
      localAutosaveSet(post.id, isPostNew, title, content, excerpt, meta);
    } else {
      await dispatch('core/editor').savePost({ isAutosave: true, ...options });

      // After autosave, save the meta to the autosave.
      const currentUser = select('core').getCurrentUser()?.id;
      const postType = select('core/editor').getCurrentPost()?.type;
      const postId = select('core/editor').getCurrentPost()?.id;
      const autosave = select('core').getAutosave(postType, postId, currentUser);

      // If no autosave, bail and do nothing!
      if (!autosave) {
        return; // eslint-disable-line consistent-return
      }

      const meta = select('core/editor').getEditedPostAttribute('meta');

      /**
       * Pass all meta to autosave endpoint so that all post meta is saved against autosave,
       * as we store all post meta against php created revisions.
       *
       * Ref: https://github.com/newscorp-ghfb/ncu-newspress-revisions/blob/d8a21d32c10264fbde06b789ea91cbd0c8010aaa/inc/class-meta.php#L269
       */
      const data = {
        post_type: postType,
        meta,
      };

      // Save meta to autosave
      api.updateAutosave(autosave?.id, data);
    }
  };
}
